   

import BackendAxios from "../repo"

const state = {
    userinfo: {},
    loader: false
}

const getters = {
    getters_userinfo: (state) => state.userinfo,
    getters_loader: (state) => state.loader,
}
const actions = {
    GET_USER_API({ commit }) {
        commit('mutation_loader', true)

        return new Promise((resolve, reject) => {
            BackendAxios.get('user/')
                .then(data => {
                    commit('mutation_userinfo', data.data)
                    commit('mutation_loader', false)
                    resolve(data)
                })
                .catch(err => {
                    reject(err)
                })
        })
    },    
}

const mutations = {
    mutation_userinfo(state, data) {
        state.userinfo = data
    },
    mutation_loader(state, data) {
        state.loader = data
    }
}

export const user__store = {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};