
   
import { mapGetters , mapActions} from 'vuex'


export const UserMixins = {
    computed: {
        ...mapGetters('user__store', ["getters_userinfo", "getters_loader"]),
        is_avatar(){
            if (this.getters_userinfo && this.getters_userinfo.social && this.getters_userinfo.social.extra_data && this.getters_userinfo.social.extra_data.picture){
                return true
            }
            return false
        },
        is_username(){
            if (this.getters_userinfo && this.getters_userinfo.social && this.getters_userinfo.social.extra_data && this.getters_userinfo.social.extra_data.name){
                return true
            }
            return false
        }
    },
    methods:{
        ...mapActions('user__store', [
            'GET_USER_API','GET_LABLES_API','GET_MESSAGE_API', 'GET_getProfile_API', 'GET_FULLMESSAGE_API'
        ]),
    }

}